import { produce } from 'immer';

export const getBoxValues = (val = {}) => Object.values(val).join(' ');

export const updatedProperty = (attr, value, ...props) => {
	if (props.length === 0) {
		return value;
	}
	const [currentProp, ...remainingProps] = props;
	if (remainingProps.length === 0) {
		return produce(attr, draft => {
			draft[currentProp] = value;
		});
	}
	return produce(attr, draft => {
		if (!Object.prototype.hasOwnProperty.call(draft, currentProp)) {
			draft[currentProp] = {};
		}
		draft[currentProp] = updatedProperty(draft[currentProp], value, ...remainingProps);
	});
};