import { __ } from '@wordpress/i18n';
import { PanelBody, RangeControl, SelectControl } from '@wordpress/components';
import { produce } from 'immer';

import { Background, BtnGroup, ColorControl, GradientControl, InlineMediaControl, Notice } from '../../../../../Components';

import { animationTypes } from '../../../utils/options';
import { types } from '../../../../../Components/Background/options';

const General = ({ attributes, setAttributes, updateObj }) => {
	const { background, wrapper, content } = attributes;
	const { type, color, gradient, image } = background['desktop'];
	const { animation = {} } = wrapper;
	const { background: contentBG } = content;
	const { type: aniType = 'none', parallax = {} } = animation;

	const updateBG = (property, value, childProperty = false) => {
		const newBG = produce(background['desktop'], draft => {
			if (false !== childProperty) {
				draft[property][childProperty] = value;
			} else {
				draft[property] = value;
			}
		});
		setAttributes({ background: { desktop: newBG } });
	}

	return <>
		<PanelBody className='pevPanelBody' title={__('Wrapper', 'background-block')}>
			<BtnGroup label={__('Type', 'background-block')} value={type} onChange={val => {
				updateBG('type', val)
				val === 'image' && image.url === '' && ['background-color: #0000;', 'background-color: #00000000;', 'background-color: #ffffff00;'].includes(contentBG) && setAttributes({ content: { ...content, background: 'background-color: #0008;' } })
			}} options={types} isIcon={true} />

			{'color' === type && <ColorControl label={__('Color', 'background-block')} value={color} onChange={val => updateBG('color', val)} defaults='#333' />}

			{'gradient' === type && <><br /><br />
				<GradientControl value={gradient} onChange={val => updateBG('gradient', val)} />
			</>}

			{'image' === type && <>
				<InlineMediaControl label={__('URL:', 'background-block')} className='mt20' value={{ url: image.url }} onChange={({ url }) => updateBG('image', url, 'url')} />

				<Notice className='mt10' notice={__('For overlay color, set content background.', 'background-block')} />

				{/* <PanelRow className='mt10'>
				<Label className=''>{__('Position', 'background-block')}</Label>

				<AlignmentMatrixControl value={image.position} onChange={val => updateBG('image', val, 'position')} defaultValue='center center' width={75} />
			</PanelRow> */}

				{/* <PanelRow className='mt10'>
				<Label className=''>{__('Attachment', 'background-block')}</Label>

				<SelectControl value={image.attachment} onChange={val => updateBG('image', val, 'attachment')} options={attachments} />
			</PanelRow> */}

				{/* <PanelRow className='mt10'>
				<Label className=''>{__('Repeat', 'background-block')}</Label>

				<SelectControl value={image.repeat} onChange={val => updateBG('image', val, 'repeat')} options={repeats} />
			</PanelRow> */}

				{/* <PanelRow className='mt10'>
				<Label className=''>{__('Size', 'background-block')}</Label>

				<SelectControl value={image.size} onChange={val => updateBG('image', val, 'size')} options={sizes} />
			</PanelRow> */}
			</>}

			<hr />

			<SelectControl className='mt20' label={__('Scroll Animation', 'background-block')} labelPosition='left' value={aniType} onChange={val => updateObj('wrapper', 'animation', val, 'type')} options={animationTypes} />
			<Notice className='mt10' notice={__('Scroll animation will not work in editor.', 'background-block')} />

			{'parallax' === aniType && <>
				{/* <br />
				<ToggleControl label={__('Reverse?', 'background-block')} checked={parallax.isReverse} onChange={val => updateObj('wrapper', 'animation', { ...parallax, isReverse: val }, 'parallax')} />
				<Notice className='mt10' notice={__('Background will scroll up faster', 'background-block')} /> */}

				<br />
				<RangeControl label={__('Speed', 'background-block')} value={parallax.speed} onChange={val => updateObj('wrapper', 'animation', { ...parallax, speed: val }, 'parallax')} min={0} max={1} step={.01} />
			</>}
		</PanelBody>


		<PanelBody className='pevPanelBody' title={__('Content', 'background-block')} initialOpen={false}>
			<Background label={__('Content / Overlay Background', 'background-block')} value={contentBG} onChange={val => updateObj('content', 'background', val)} enabled={['color', 'gradient']} />
		</PanelBody>
	</>
}
export default General;