import { withSelect } from '@wordpress/data';
import { __ } from '@wordpress/i18n';
import { PanelBody, __experimentalUnitControl as UnitControl, __experimentalBoxControl as BoxControl } from '@wordpress/components';

import { DeviceControl } from '../../../../../Components';
import { emUnit, pxUnit, vhUnit } from '../../../../../Components/utils/options';

const paddingDefault = {
	desktop: {
		top: '25px',
		right: '40px',
		bottom: '25px',
		left: '40px'
	},
	tablet: {
		top: '20px',
		right: '30px',
		bottom: '20px',
		left: '30px'
	},
	mobile: {
		top: '15px',
		right: '20px',
		bottom: '15px',
		left: '20px'
	}
}

const Style = ({ attributes, updateObj, device }) => {
	const { wrapper, content } = attributes;
	const { minHeight, padding: wrapPadding } = wrapper;
	const { padding: contentPadding } = content;

	return <>
		<PanelBody className='pevPanelBody' title={__('Wrapper', 'background-block')}>
			<UnitControl label={__('Min Height:', 'parallax-section')} labelPosition='left' value={minHeight} onChange={val => updateObj('wrapper', 'minHeight', val)} units={[pxUnit(700), emUnit(45), vhUnit(100)]} isResetValueOnUnitChange={true} />

			<div className='pevBoxControl mt20'>
				<BoxControl label={<>
					{__('Padding', 'background-block')}
					<DeviceControl className='ml5' />
				</>} values={wrapPadding[device]} onChange={val => updateObj('wrapper', 'padding', val, device)} resetValues={paddingDefault[device]} />
			</div>
		</PanelBody>


		<PanelBody className='pevPanelBody' title={__('Content', 'background-block')} initialOpen={false}>
			<div className='pevBoxControl'>
				<BoxControl label={<>
					{__('Padding', 'background-block')}
					<DeviceControl className='ml5' />
				</>} values={contentPadding[device]} onChange={val => updateObj('content', 'padding', val, device)} resetValues={paddingDefault[device]} />
			</div>
		</PanelBody>
	</>
}
export default withSelect((select) => {
	const { __experimentalGetPreviewDeviceType } = select('core/edit-post');

	return {
		device: __experimentalGetPreviewDeviceType()?.toLowerCase()
	}
})(Style);