import { __ } from '@wordpress/i18n';
import { MediaUpload, MediaUploadCheck } from '@wordpress/block-editor';
import { Button, PanelRow, TextControl } from '@wordpress/components';

import './style.scss';
import Label from '../Label';
import { uploadIcon } from '../utils/icons';

export const InlineMediaControl = ({ className = '', label = '', value = {}, onChange, types = ['image'], placeholder = __('Enter URL', 'pev') }) => {
	return <div className={`pevInlineMediaControl ${className}`}>
		{label && <Label className='mb5'>{label}</Label>}

		<PanelRow>
			<TextControl value={value.url} onChange={url => onChange({ id: null, url, alt: '', title: '' })} placeholder={placeholder} />

			<MediaUploadCheck>
				<MediaUpload
					onSelect={({ id, url, alt, title }) => onChange({ id, url, alt, title })}
					allowedTypes={types}
					render={({ open }) => <Button onClick={open}>{uploadIcon}</Button>}
				/>
			</MediaUploadCheck>
		</PanelRow>
	</div>
}