import { useEffect, useState } from 'react';
import { __ } from '@wordpress/i18n';
import { PanelRow, __experimentalAlignmentMatrixControl as AlignmentMatrixControl, SelectControl } from '@wordpress/components';

import { BtnGroup, ColorControl, GradientControl, InlineMediaControl, Label } from '../index';
import { types, repeats, sizes } from './options';

const Background = ({ className = '', label = '', value, onChange, enabled = ['color', 'gradient', 'image'] }) => {
	const [type, setType] = useState(enabled[0] || 'color');
	const [color, setColor] = useState('#5465ff');
	const [gradient, setGradient] = useState('linear-gradient(135deg, #5465ff 0%, #788bff 100%)');
	const [image, setImage] = useState({ url: '#', position: 'center center', size: 'cover', repeat: 'no-repeat' });

	const isColor = enabled.includes('color');
	const isGradient = enabled.includes('gradient');
	const isImage = enabled.includes('image');

	// Get value
	useEffect(() => {
		if (typeof value === 'string') {
			if (value.startsWith('background-image: linear') || value.startsWith('background-image: radial') || value.startsWith('background-image: conic')) {
				setType('gradient');

				setGradient(value.replace('background-image: ', '').replace(';', ''))
			} else if (value.startsWith('background:')) {
				setType('image');

				const pattern = /background: url\(['"]?(.*?)['"]?\) ([^\/]+) ([^\/]+) \/ ([^ ]+) ([^ ]+)/;

				const match = value.match(pattern);

				if (match?.length > 5) {
					setImage({ url: match[1], position: `${match[2]} ${match[3]}`, size: match[4], repeat: match[5] })
				} else {
					setImage({ url: '', position: 'center center', size: 'cover', repeat: 'no-repeat' })
				}
			} else {
				setType('color');

				setColor(value.replace('background-color: ', '').replace(';', ''))
			}
		}
	}, []);

	// Generate value
	useEffect(() => {
		if ('image' === type) {
			onChange(`background: url("${image.url}") ${image.position} / ${image.size} ${image.repeat};`);
		} else if ('gradient' === type) {
			onChange(`background-image: ${gradient};`);
		} else {
			onChange(`background-color: ${color};`);
		}
	}, [type, color, gradient, image]);

	return <div className={`pevBackground ${className}`}>
		{label && <strong><Label className='mb10'>{label}</Label></strong>}

		<BtnGroup label={__('Type', 'pev')} value={type} onChange={val => setType(val)} options={types.filter(t => enabled.includes(t.value))} isIcon={true} />

		{'color' === type && isColor && <ColorControl className='mt10' label={__('Color:', 'pev')} value={color} onChange={val => setColor(val)} />}

		{'gradient' === type && isGradient && <GradientControl className='mt10' value={gradient} onChange={val => setGradient(val)} />}

		{'image' === type && isImage && <>
			<InlineMediaControl label={__('URL:', 'pev')} className='mt10' value={{ url: image.url }} onChange={({ url }) => setImage({ ...image, url })} />

			<PanelRow className='mt10'>
				<Label className=''>{__('Position', 'pev')}</Label>

				<AlignmentMatrixControl value={image.position} onChange={val => setImage({ ...image, position: val })} defaultValue='center center' width={75} />
			</PanelRow>

			<PanelRow className='mt10'>
				<Label className=''>{__('Repeat', 'pev')}</Label>

				<SelectControl value={image.repeat} onChange={val => setImage({ ...image, repeat: val })} options={repeats} />
			</PanelRow>

			<PanelRow className='mt10'>
				<Label className=''>{__('Size', 'pev')}</Label>

				<SelectControl value={image.size} onChange={val => setImage({ ...image, size: val })} options={sizes} />
			</PanelRow>
		</>}
	</div>
}
export default Background;