import { __ } from '@wordpress/i18n';
import { withSelect } from '@wordpress/data';
import { ColorPalette, ColorIndicator, Dropdown, PanelRow, Button } from '@wordpress/components';

import './style.scss';
import Label from '../Label';

const ColorControl = ({ className = '', label = '', value = '#333', onChange, defaults = '', isAlpha = true, limit = 5, colors }) => {
	return <PanelRow className={`pevColorControl ${className}`}>
		{label && <Label className=''>{label}</Label>}

		<Dropdown
			className="pevDropdownContainer"
			contentClassName="pevDropdownPopover"
			popoverProps={{ placement: 'top-end' }}
			renderToggle={({ isOpen, onToggle }) => <div className='pevResetContainer'>
				<ColorIndicator className='colorIndicator' onClick={onToggle} aria-expanded={isOpen} colorValue={value} />

				{defaults && (defaults !== value) && <Button className='pevResetBtn' icon='image-rotate' label={__('Reset', 'pev')} onClick={() => onChange(defaults)} />}
			</div>}
			renderContent={() => <ColorPalette
				colors={[{ name: 'Plugin Envision', slug: 'plugin-envision', color: '#5465ff' }, ...colors].slice(0, limit)}
				value={value}
				onChange={val => onChange(val)}
				enableAlpha={isAlpha}
			/>}
		/>
	</PanelRow>
}
export default withSelect((select) => {
	return {
		colors: select('core/block-editor').getSettings().colors
	}
})(ColorControl);