import { useEffect } from 'react';
import { useBlockProps, InnerBlocks } from '@wordpress/block-editor';

import Style from '../Common/Style';
import Settings from './Settings/Settings';

const Edit = (props) => {
	const { attributes, setAttributes, clientId } = props;
	const { wrapper } = attributes;
	const { animation = {} } = wrapper;
	const { type: aniType = 'none' } = animation;

	useEffect(() => { clientId && setAttributes({ clientId: clientId.slice(0, 10) }); }, [clientId]);

	return <>
		<Settings attributes={attributes} setAttributes={setAttributes} />

		<div {...useBlockProps()} className={useBlockProps().className?.replace('wp-block-evb-background', '')}>
			<div className={`wp-block-evb-background scroll-${aniType}`} id={`evbBackground-${clientId}`}>
				<Style attributes={attributes} clientId={clientId} />

				<div className='evbBackground' />

				<div className='backgroundContent'>
					<InnerBlocks template={[
						['core/heading', {
							content: 'Background Block By Plugin Envision',
							style: {
								typography: { fontSize: '40px' },
								color: { text: '#fff' }
							}
						}],

						['core/paragraph', {
							content: 'Elevate the visual appeal of your WordPress website effortlessly with the Background Block for Gutenberg. This versatile block empowers you to add captivating backgrounds to sections, columns, or entire pages, enhancing the overall aesthetics and user experience.',
							style: {
								typography: { fontSize: '20px' },
								color: { text: '#fff' }
							}
						}],
						['core/buttons', { contentJustification: 'center', layout: { justifyContent: 'center' } }, [
							['core/button', {
								text: 'Click Me',
								href: 'https://pluginenvision.com',
								style: {
									typography: { fontSize: '28px', letterSpacing: '2px' },
									color: { text: '#5465ff', background: '#fff' },
									border: { radius: '100px' },
									spacing: { padding: { left: 'var:preset|spacing|30', right: 'var:preset|spacing|30' } }
								}
							}]
						]]
					]} />
				</div>
			</div>
		</div>
	</>
}
export default Edit;