import { __ } from '@wordpress/i18n';
import { PanelRow, Button, Dashicon } from '@wordpress/components';

import './style.scss';
import { copyIcon, trashIcon } from '../utils/icons';

export const ItemAction = ({ attributes, setAttributes, property, index, setActiveIndex = false }) => {
	const items = attributes[property];

	const duplicateItem = e => {
		e.preventDefault();

		setAttributes({ [property]: [...items.slice(0, index), { ...items[index] }, ...items.slice(index)] });

		setActiveIndex && setActiveIndex(index + 1);
	}

	const removeItem = e => {
		e.preventDefault();

		setAttributes({ [property]: [...items.slice(0, index), ...items.slice(index + 1)] });

		setActiveIndex && setActiveIndex(0 === index ? 0 : index - 1);
	}

	return <PanelRow className='pevItemAction mt20 mb15'>
		<Button className='duplicateItem' label={__('Duplicate', 'pev')} onClick={duplicateItem}>
			{copyIcon}
			{__('Duplicate', 'pev')}
		</Button>

		{1 < items?.length && <Button className='removeItem' label={__('Remove', 'pev')} onClick={removeItem}>
			{trashIcon}
			{__('Remove', 'pev')}
		</Button>}
	</PanelRow>
}

export const AddItem = ({ attributes, setAttributes, property, newItem, itemName = 'Item', setActiveIndex = false }) => {
	const onAddItem = () => {
		setAttributes({ [property]: [...attributes[property], newItem] });
		setActiveIndex && setActiveIndex(attributes[property].length);
	}

	return <div className='pevAddItem'>
		<Button label={__(`Add New ${itemName}`, 'pev')} onClick={onAddItem}><Dashicon icon='plus' size={23} />{__(`Add New ${itemName}`, 'pev')}</Button>
	</div>
} 