import { getBoxValues } from '../../../../Components/utils/functions';

const Style = ({ attributes, clientId }) => {
	const { background, wrapper, content } = attributes;
	const { type, color, gradient, image } = background['desktop'];
	const { url, position, attachment, repeat, size } = image || {};
	const { minHeight, padding: wrapPadding, animation = {} } = wrapper;
	const { parallax = {} } = animation;
	const { background: contentBG, align, padding: contentPadding } = content;

	const mainSl = `#evbBackground-${clientId}`;
	const backgroundSl = `${mainSl} .evbBackground`;
	const contentSl = `${mainSl} .backgroundContent`;

	const bgStyle = 'image' === type ? `url(${url})` :
		'gradient' === type ? gradient :
			color;

	const bgImgStyles = 'image' === type ? `
		background-position: ${position};
		background-attachment: ${attachment};
		background-repeat: ${repeat};
		background-size: ${size};
	` : '';

	return <style dangerouslySetInnerHTML={{
		__html: `
		${mainSl}{
			display: flex;
			min-height: ${minHeight};
			padding: ${getBoxValues(wrapPadding.desktop)};
		}
		${mainSl}.scroll-parallax .evbBackground{
			height: calc( 100% + ( 100% * ${parallax.speed ?? 1} ) );
		}
		${backgroundSl}{
			width: 100%;
			height: 100%;
			position: absolute;
			top: 0;
			left: 0;
			background: ${bgStyle};
			${bgImgStyles}
		}
		${contentSl}{
			flex: 1;
			display: flex;
			flex-direction: column;
			text-align: ${align.text};
			justify-content: ${align.vertical};
			${contentBG}
			padding: ${getBoxValues(contentPadding.desktop)};
		}
		@media (min-width: 481px) and (max-width: 960px) {
			${mainSl}{
				padding: ${getBoxValues(wrapPadding.tablet)};
			}
			${contentSl}{
				padding: ${getBoxValues(contentPadding.tablet)};
			}
		}
		@media (max-width: 480px) {
			${mainSl}{
				padding: ${getBoxValues(wrapPadding.mobile)};
			}
			${contentSl}{
				padding: ${getBoxValues(contentPadding.mobile)};
			}
		}
	`}} />;
}
export default Style;