import { __ } from '@wordpress/i18n';
import { InspectorControls, BlockControls, AlignmentToolbar } from '@wordpress/block-editor';
import { TabPanel } from '@wordpress/components';
import { produce } from 'immer';

import { tabs } from '../../../utils/options';
import { verticalTopIcon, verticalCenterIcon, verticalBottomIcon } from '../../../utils/icons';
import General from './General';
import Style from './Style';

const Settings = ({ attributes, setAttributes }) => {
	const { content } = attributes;
	const { align } = content;

	const updateObj = (attr, property, value, childProp = false) => {
		const newAttr = produce(attributes[attr], draft => {
			if (false !== childProp) {
				draft[property][childProp] = value;
			} else {
				draft[property] = value;
			}
		});
		setAttributes({ [attr]: newAttr });
	}

	const props = { attributes, setAttributes, updateObj }

	return <>
		<InspectorControls className='pevBlockSidebar'>
			{/* <div className='pevInspectorInfo'></div> */}

			<TabPanel className='pevTabPanel' activeClass='activeTab' tabs={tabs}>{tab => <>
				{'general' === tab.name && <General {...props} />}


				{'style' === tab.name && <Style {...props} />}
			</>}</TabPanel>
		</InspectorControls>


		<BlockControls>
			<AlignmentToolbar value={align.vertical} onChange={val => updateObj('content', 'align', val, 'vertical')} describedBy={__('Vertical Alignment')} alignmentControls={[
				{ title: __('Content in top', 'background-block'), align: 'flex-start', icon: verticalTopIcon },
				{ title: __('Content in center', 'background-block'), align: 'center', icon: verticalCenterIcon },
				{ title: __('Content in bottom', 'background-block'), align: 'flex-end', icon: verticalBottomIcon }
			]} />

			<AlignmentToolbar value={align.text} onChange={val => updateObj('content', 'align', val, 'text')} />
		</BlockControls>
	</>
}
export default Settings;