/**
 * @props className (optional): '' (String)
 * @props label (optional) (String)
 * @props value (String)
 * @return onChange: (Function)
 * @props options (Object)
 * @props size (String)
 * @props isIcon (Boolean)
 */

import { PanelRow, ButtonGroup, Button } from '@wordpress/components';

import './style.scss';
import { Label } from '../index';

const BtnGroup = (props) => {
	const { className, label = '' } = props;

	return label ? <PanelRow className={className}>
		<Label className=''>{label}</Label>

		<Buttons {...props} />
	</PanelRow> : <Buttons {...props} />
}
export default BtnGroup;

const Buttons = (props) => {
	const { className, value, onChange, options, size, isIcon = false } = props;

	return <ButtonGroup className={`pevBtnGroup ${className || null}`}>
		{Object.values(options).map(obj => {
			const { value: val, label = '', icon = '' } = obj;
			const isActive = val === value;
			const isSm = 'small' === size;

			return <Button
				key={val}
				label={label}
				showTooltip={true}
				tooltipPosition='top'
				variant={isActive ? 'primary' : ''}
				isPressed={isActive}
				size={'small' === size ? 'small' : 'compact'}
				onClick={() => onChange(val)}
				icon={isIcon && typeof icon === 'string' ? icon : false}
			>
				{isIcon && typeof icon !== 'string' ? icon : ''}
				{!isIcon && label}
			</Button>
		})}
	</ButtonGroup>
}