import { __ } from '@wordpress/i18n';
import { withSelect } from '@wordpress/data';
import { GradientPicker } from '@wordpress/components';

const GradientControl = ({ className = '', value = '', onChange, disableCustomGradients = false, clearable = false, limit = 6, gradients }) => {
	return <GradientPicker
		className={className}
		__nextHasNoMargin
		value={value}
		onChange={val => onChange(val)}
		gradients={[{
			name: 'Plugin Envision',
			gradient: 'linear-gradient(135deg, #5465ff 0%, #bfd7ff 100%)',
			slug: 'plugin-envision',
		}, ...gradients].slice(0, limit)}
		disableCustomGradients={disableCustomGradients}
		clearable={clearable}
	/>
}
export default withSelect((select) => {
	return {
		gradients: select('core/block-editor').getSettings().gradients
	}
})(GradientControl);