import { Button } from '@wordpress/components';
import { compose } from '@wordpress/compose';
import { withSelect, withDispatch } from '@wordpress/data';

import './style.scss';
import { desktopIcon, tabletIcon, mobileIcon } from '../utils/icons';

const devices = [
	{ name: 'desktop', label: 'Desktop', icon: desktopIcon },
	{ name: 'tablet', label: 'Tablet', icon: tabletIcon },
	{ name: 'mobile', label: 'Mobile', icon: mobileIcon }
];

const DeviceControl = ({ className = '', device, setDevice }) => {
	return <div className={`pevDeviceControl ${className}`}>
		{devices.map(opt => {
			const { name, label, icon } = opt;
			return <Button className={device === name ? 'active' : ''} onClick={() => setDevice(label)}>{icon}</Button>
		})}
	</div>
}
export default compose(
	withSelect((select) => {
		const { __experimentalGetPreviewDeviceType } = select('core/edit-post');

		return {
			device: __experimentalGetPreviewDeviceType()?.toLowerCase()
		}
	}),
	withDispatch((dispatch) => {
		const { __experimentalSetPreviewDeviceType } = dispatch('core/edit-post');

		return {
			setDevice: (device) => __experimentalSetPreviewDeviceType(device)
		}
	})
)(DeviceControl);